body {
  margin: 0;
  padding: 0;
  background-color: #F7F7F7;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}

.react-codemirror2 {
    font-size: 24px;
    flex: 1 1;
}

.react-codemirror2 .CodeMirror {
    height: 100%;
}

.editor-highlighted-background {
    background-color: #44535a;
}

@media (max-width: 700px) {
    .react-codemirror2 {
        font-size: 13px;
    }
}

strong {
    font-weight: 500;
}

#burst-12 {
    background: red;
    width: 450px;
    height: 450px;
    position: relative;
    text-align: center;
    z-index: -1;
}

#burst-12:before, #burst-12:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 450px;
    width: 450px;
    background: red;
    z-index: -1;
}

#burst-12:before {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
}

#burst-12:after {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
}
:root {
    --main: #2A363B;
    --dark-main: #1F292E;
    --one: #A7226E;
    --two: #EC2049;
    --three: #F26B38;
    --four: #F7DB4F;
    --five: #2F9599;
    --gray: #A8A7A7;
    --clear-text-color: #FFF;
    --dark-text-color: #000
}
.rst__rowContents {
    color: var(--clear-text-color);
    background-color: var(--main);
    border: none;
    border-radius: 10px;
    min-width: 0px;
    padding: 0 5px 0 25px;
}

.rst__rowTitle {
    font-weight: normal;
}

.Program .rst__rowContents {
    background-color: var(--dark-main);
}

.VariableDeclarator .rst__rowContents {
    background-color: var(--five);
}

.arguments .rst__rowContents {
    background-color: var(--three);
}

.BinaryExpression .rst__rowContents {
    background-color: var(--four);
    color: var(--dark-text-color);
}

.UnaryExpression .rst__rowContents {
    background-color: var(--four);
    color: var(--dark-text-color);
}

.AssignmentExpression .rst__rowContents {
    background-color: var(--four);
    color: var(--dark-text-color);
}

.Identifier .rst__rowContents {
    background-color: var(--one);
}

.Literal .rst__rowContents {
    background-color: var(--two);
}

.highlighted-code {
    background-color: #44535a;
}


